import { html, TemplateResult } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

export type TFilterButtonTemplateArgs = Partial<HTMLMarketFilterButtonElement> & {
  feedbackText?: string | TemplateResult;
  labelText?: string | TemplateResult;
};

export const FilterButtonTemplate = ({
  disabled,
  feedbackText,
  iconOnly,
  labelText,
  size,
}: TFilterButtonTemplateArgs) => html`
  <market-filter-button ?disabled=${disabled} ?icon-only=${iconOnly} size=${ifDefined(size)}>
    ${labelText} ${feedbackText ? html`<span slot="feedback">${feedbackText}</span>` : ''}
  </market-filter-button>
`;
